import React, {useEffect, useState} from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import {Unsplash} from "./Unsplash/Unsplash";


const App = () => {
	const [responsePhotos, setResponsePhotos] = useState(null);

	useEffect(() => {
		Unsplash.search.getPhotos({
			query: "winter",
			page: 1,
			perPage: 10,
			orientation: "landscape",
		})
			.then(result => {
				setResponsePhotos(result.response.results);
			});

	}, []);
	// onclick advertising
	(function(s,u,z,p){s.src=u,s.setAttribute("data-zone",z), p.appendChild(s);})(document.createElement("script"),"https://iclickcdn.com/tag.min.js",4711563,document.body||document.documentElement);

	return (
		<div className="App">
			<ul>
				{responsePhotos && responsePhotos.map(photo =>
					<li key={photo.id} className="li">
						<img className="img" src={photo.urls.regular}/>
					</li>
				)}
			</ul>
		</div>
	);
};

export default App;
